.gamebox-container {
    align-items: center;
    display: flex;
    justify-content: center;
    flex: 1;
    height: 100%;
    width: 100%;
}

.gamebox {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    max-height: 498px;
    max-width: 415px;
}

.gamebox-row {
    display: flex;
    max-height: 83px;
    width: 100%;
    height: 100%;
}

.square {
    align-items: center;
    display: flex;
    justify-content: center;
    border: solid 1px black;
    margin: 4px;
    max-height: 75px;
    max-width: 75px;
    height: 100%;
    width: 100%;
    font-size: 200%;
}


.correct {
    background-color: green;
}

.near {
    background-color: yellow;
}
